<template>
	<div id="box">
		<div class="box" v-for='item,index in $store.state.data'>
			<span>{{item.name}}</span>
			<button @click='add(item.id)'>+</button>
			<span>{{item.count}}</span>
			<button @click='remove(item.id)'>-</button>
			<span>￥{{item.price}}</span>
		</div>
		<p>总价{{sum}}</p>
	</div>
</template>
<script>
	import {mapMutations,mapGetters} from 'vuex';
	export default{
		data(){
			return {
				//num: this.$store.state.data
			}
		},
		methods: {
			...mapMutations({
				adds: 'adds',
				removes: 'removes'
			}),
			add(id){
				//this.$store.commit('add',id);
				//this.adds(id);
				this.$store.dispatch('changAdd',id);
			},
			remove(id){
				//this.$store.commit('remove',id);
				this.removes(id);
			}
		},
		computed: {
			...mapGetters(['sum'])
		}
	}
</script>
<style>
	button{
		width: 50px;
	}
</style>


